<template>
<div id="ref-tables">
    <h2><b>Tra cu</b></h2>

	<b-tabs>
		<b-tab title="Trng">
			<b-row>
				<b-col sm="5" offset-sm="7">
					<b-input-group>
						<b-input-group-addon><icon name="search"></icon></b-input-group-addon>
						<b-form-input type="text" placeholder="Tm kim" v-model="schoolQuery"
							v-on:keyup.enter.native="search('searchedSchools', 'schoolQuery', '/api/schools/search')">
						</b-form-input>
						<b-input-group-addon>
							<b-link href="#" v-on:click="resetSearch('searchedSchools', 'schoolQuery')">
								Hin tt c
							</b-link>
						</b-input-group-addon>
					</b-input-group>
				</b-col>
			</b-row>

			<div class="table-wrapper">
				<b-table striped hover :items="schoolDisp" :fields="schoolFields">
					<template slot="show_details" slot-scope="row">
						<b-form-checkbox v-model="row.item._showDetails"></b-form-checkbox>
					</template>
					<template slot="row-details" slot-scope="row">
						<b-card>
							<b-table :items="row.item.majors" :fields="schoolMajorFields"></b-table>
						</b-card>
					</template>
				</b-table>
			</div>
		</b-tab>

		<b-tab title="Ngnh">
			<b-row>
				<b-col sm="5" offset-sm="7">
					<b-input-group>
						<b-input-group-addon><icon name="search"></icon></b-input-group-addon>
						<b-form-input type="text" placeholder="Tm kim" v-model="majorQuery"
							v-on:keyup.enter.native="search('searchedMajors', 'majorQuery', '/api/majors/search')">
						</b-form-input>
						<b-input-group-addon>
							<b-link href="#" v-on:click="resetSearch('searchedMajors', 'majorQuery')">
								Hin tt c
							</b-link>
						</b-input-group-addon>
					</b-input-group>
				</b-col>
			</b-row>

			<div class="table-wrapper">
				<b-table striped hover :items="majorDisp" :fields="majorFields">
					<template slot="show_details" slot-scope="row">
						<b-form-checkbox v-model="row.item._showDetails"></b-form-checkbox>
					</template>
					<template slot="row-details" slot-scope="row">
						<b-card>
							<b-table :items="row.item.schools" :fields="majorSchoolFields"></b-table>
						</b-card>
					</template>
				</b-table>
			</div>
		</b-tab>
	</b-tabs>
</div>
</template>

<script>
import 'vue-awesome/icons/search'
import $ from 'jquery'

export default {
	name: 'RefTables',
	data() {
		return {
			schools: null,
			schoolFields: {
				name: {label: 'Tn trng'},
				fee: {label: "Mc hc ph (1 TC)"},
				ratio: {label: "T l chi"},
				rank_score: {label: "im xp hng"},
				show_details: {label: "Hin danh sch ngnh"}
			},
			schoolMajorFields: {
				name: {label: 'Ngnh'},
				score_2015: {label: 'im chun 2015'},
				score_2016: {label: 'im chun 2016'},
				cutoff: {label: 'Ch tiu'},
				group: {label: 'Khi'}
			},
			searchedSchools: null,
			schoolQuery: null,
			majors: null,
			majorFields: {
				name: {label: 'Tn ngnh'},
				group: {label: 'Khi'},
				show_details: {label: "Hin danh sch trng"}
			},
			majorSchoolFields: {
				name: {label: 'Trng'},
				ratio: {label: "T l chi"},
				score_2015: {label: 'im thi 2015'},
				score_2016: {label: 'im thi 2016'},
				rank_score: {label: "im xp hng"},
				cutoff: {label: 'Ch tiu'}
			},
			searchedMajors: null,
			majorQuery: null
		}
	},
	computed: {
		schoolDisp() {
			if (this.searchedSchools) {
				return this.searchedSchools;
			} else if (this.schools) {
				return this.schools;
			} else return null;
		},
		majorDisp() {
			if (this.searchedMajors) {
				return this.searchedMajors;
			} else if (this.majors) {
				return this.majors;
			} else return null;
		}
	},
	methods: {
		resetSearch(key, queryKey) {
			this[key] = null;
			this[queryKey] = null;
		},
		search(key, queryKey, apiUrl) {
			if (!this[queryKey]) return;
			// console.log(apiUrl);
			var component = this;
			$.getJSON(apiUrl + '?query=' + this[queryKey], function(data){
				component[key] = data;
			});
		}
	},
	mounted() {
		var component = this;
		$.getJSON('/api/schools/all', function(data) {
			component.schools = data;
		})
		$.getJSON('/api/majors/all', function(data) {
			component.majors = data;
		})
	}
}
</script>

<style scoped>
#ref-tables {
	padding: 10px;
}

.table-wrapper {
	margin-top: 5px;
	max-height: 70vh;
	overflow-y: scroll;
	overflow-x: auto;
}
</style>



